<h1 id="git-basics" tabindex="-1"><a class="header-anchor" href="#git-basics" aria-hidden="true">#</a> Git Basics</h1>
<p>Git is a version control system you can use to remember previous versions of the source code (or whatever type of files you need to version control) in your projects. It was created by <a href="https://en.wikipedia.org/wiki/Linus_Torvalds" target="_blank" rel="noopener noreferrer">Linus Torvalds<OutboundLink/></a> in 2005 when he couldn't find a good version control system he could use for the Linux kernel he was working on. Git quickly became a popular version control system, especially for open source projects thanks to GitHub hosting the source code online for free. <a href="https://trends.google.com/trends/explore?date=all&amp;q=%2Fm%2F05vqwg,%2Fm%2F012ct9,%2Fm%2F08441_,%2Fm%2F08w6d6,%2Fm%2F09d6g&amp;hl=en-US&amp;tz=&amp;tz=" target="_blank" rel="noopener noreferrer">Git is today (2019) one of the most commonly used version control systems.<OutboundLink/></a></p>
<h2 id="why-use-a-version-control-system" tabindex="-1"><a class="header-anchor" href="#why-use-a-version-control-system" aria-hidden="true">#</a> Why use a version control system?</h2>
<p>So, why bother spending time on learning Git? Turns out that using a version control system have some very valuable benefits for projects, and all big software projects do today use some type of version control system.</p>
<div class="custom-container warning"><p class="custom-container-title">Just examples</p>
<p>The benefits described below are just examples, and not a complete list of all the benefits with using a version control system.</p>
</div>
<h3 id="benefit-1-knowing-when-what-changed" tabindex="-1"><a class="header-anchor" href="#benefit-1-knowing-when-what-changed" aria-hidden="true">#</a> Benefit 1: Knowing when what changed</h3>
<p>If a security bug is introduced into software, we can use the version control system to go back in time and see for how long time the bug has existed. This is especially important for libraries which other applications  makes use of, because the libraries will most likely exist in different versions, and applications making use of the library need to know if the specific version of the library they are using contains the vulnerability or not.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>When <a href="http://heartbleed.com/" target="_blank" rel="noopener noreferrer">The Heartbleed Bug<OutboundLink/></a> was discovered in OpenSSL, they could quickly say:</p>
<blockquote>
<p>Bug was introduced to OpenSSL in December 2011 and has been out in the wild since OpenSSL release 1.0.1 on 14th of March 2012. OpenSSL 1.0.1g released on 7th of April 2014 fixes the bug.</p>
</blockquote>
</div>
<p>With the same reasoning, applications need to version control their code as well, so if, for example, a website contains a security vulnerability, one can quickly see for how long time the vulnerability has existed. Maybe only newly registered users since the bug was introduced have had their data compromised?</p>
<h3 id="benefit-2-keeping-track-of-who-did-what" tabindex="-1"><a class="header-anchor" href="#benefit-2-keeping-track-of-who-did-what" aria-hidden="true">#</a> Benefit 2: Keeping track of who did what</h3>
<p>When you find a vulnerability/bug, you always want to have as much information as possible about it, including who introduced the bug. Was it on purpose or not? What relevant code was introduced? With a version control system, that is quite easy to find out.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>When a new developer took over the npm package <em>event-stream</em> (a library in Node.js), he started to inject bad code into it, which was executed by all new applications (and some old ones that was updated) using this library. See <a href="https://github.com/dominictarr/event-stream/issues/116" target="_blank" rel="noopener noreferrer">issue #116 on the event-stream's GitHub repository<OutboundLink/></a>. Thanks to using a version control system, it was quite easy to discover all changes made by that programmer, and his changes to the code could be reverted back.</p>
</div>
<h3 id="benefit-3-dealing-with-concurrent-changes" tabindex="-1"><a class="header-anchor" href="#benefit-3-dealing-with-concurrent-changes" aria-hidden="true">#</a> Benefit 3: Dealing with concurrent changes</h3>
<p>In bigger software projects, there are often multiple programmers working on the source code at the same time. Each of them have their own copy of the source code, and when one programmer is done implementing a new feature, she needs to share her version of the source code with the other programmers, so their copy of the source code contains the new feature as well. But at the same time, these other programmers will be half way through implementing their own features, which might clash with the changes made by the first programmer. This is a non-trivial problem to solve, but version control systems gives us good tools to deal with it.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p><a href="https://royal.pingdom.com/linux-kernel-development-numbers/" target="_blank" rel="noopener noreferrer">1 316 developers were involved in the development of the Linux kernel version 3.2<OutboundLink/></a>.</p>
</div>
<h3 id="benefit-4-backups" tabindex="-1"><a class="header-anchor" href="#benefit-4-backups" aria-hidden="true">#</a> Benefit 4: Backups</h3>
<p>By using a <em>distributed</em> version control system (such as Git) you automatically get backups of your code. Each programmer that works on the project has a copy of the source code (and the previous versions of it) on her own computer. So if 10 programmers work on the same project, you have (at least) 10 copies of the source code on 10 different computers, so if one programmer's computer crashes and the source code on that computer is lost, there are still 9 copies left on the other computers. Your source code will never be truly lost again.</p>
<h2 id="how-does-git-work" tabindex="-1"><a class="header-anchor" href="#how-does-git-work" aria-hidden="true">#</a> How does Git work?</h2>
<p><a href="https://en.wikipedia.org/wiki/Comparison_of_version-control_software#General_information" target="_blank" rel="noopener noreferrer">There exists many different version control systems<OutboundLink/></a>, but let us focus on Git alone. Many other version control systems work in a similiar way and often use the same terms, but each term might have a little bit different meaning.</p>
<p>In the text below the term <em>project folder</em> refers to a folder on your computer in which you put all the files that belongs to a project you are working on. If the project for example is about creating a new website, then the project folder could be a folder named <code>the-project</code>, and in this folder you put all the source files (in this case all the HTML files, CSS files, images, etc.) the website consists of.</p>
<div class="custom-container tip"><p class="custom-container-title">Installing Git</p>
<p>Download and install Git from <a href="https://git-scm.com/" target="_blank" rel="noopener noreferrer">git-scm.com<OutboundLink/></a>.</p>
</div>
<h3 id="repositories" tabindex="-1"><a class="header-anchor" href="#repositories" aria-hidden="true">#</a> Repositories</h3>
<p>Version control systems make use of <em>repositories</em>. When you have a project you want to version control, you tell your version control system to create a new repository for you for that project. The repository is responsible for remembering all the different versions you have of your source files. So a single computer can contain multiple repositories: one for each project you version control.</p>
<p>How a version control system internally represents a repository is not something we need to worry that much about, but it is good to know that Git uses a folder named <code>.git</code> in the project folder for this. The repository will contain all the different versions of the source files you want to remember. If you later delete the <code>.git</code> folder in your project folder, then you have deleted your entire repository for that project, and all the versions of your source files you have asked Git to remember for you will be gone. Be careful to not delete a <code>.git</code> folder by accident!</p>
<p>To create a new Git repository for your project, go to the project folder and run the command <code>git init</code>. This will create the <code>.git</code> folder in your project folder for you. So far we haven't asked Git to remember any new version of the project for us, so the repository will at the moment be an empty repository (although the <code>.git</code> folder will already contain some files Git uses internally).</p>
<div class="custom-container warning"><p class="custom-container-title">Note</p>
<p>Since the name of the <code>.git</code> folder starts with a dot, most file explorers don't display this folder unless you change a setting in your file explorer telling it to display files and folders that starts with a dot. How you change that settings depends on which file explorer you use (Google it...).</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<CodeGroup>
<CodeGroupItem title="Windows PowerShell">
<div class="language-powershell ext-powershell line-numbers-mode"><pre v-pre class="language-powershell"><code><span class="token comment"># Which directory are we currently standing in?</span>
$ <span class="token string">"<span class="token variable">$pwd</span>"</span>
    C:\Users\peter\the<span class="token operator">-</span>project

<span class="token comment"># What does that directory contain?</span>
$ <span class="token function">dir</span> <span class="token operator">-</span>Name <span class="token operator">-</span>Force
    <span class="token comment"># (no output = nothing)</span>

<span class="token comment"># Create a new repository for the directory we are standing in.</span>
$ git init
    Initialized empty Git repository in C:<span class="token operator">/</span>Users<span class="token operator">/</span>peter<span class="token operator">/</span>the<span class="token operator">-</span>project<span class="token operator">/</span><span class="token punctuation">.</span>git<span class="token operator">/</span>

<span class="token comment"># What does the directory contain now?</span>
$ <span class="token function">dir</span> <span class="token operator">-</span>Name <span class="token operator">-</span>Force
    <span class="token punctuation">.</span>git
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br></div></div></CodeGroupItem>
