<h3 id="commits-versions" tabindex="-1"><a class="header-anchor" href="#commits-versions" aria-hidden="true">#</a> Commits (versions)</h3>
<p>To tell the repository to remember a specific version of your project, you send the repository a <em>commit</em>. A commit describes the changes made to project since the previous commit. This way, each commit can be seen as a snapshot of what the project look like at the moment the commit was created. By using these commits Git is able to restore the source files so they contain precisely the same content as they did at a specific commit/version of your project.</p>
<p>A commit in Git primarily contains:</p>
<ul>
<li>A unique identifier for the commit (a hash value like the hexadecimal string <code>e4728bc8aa2...</code>)</li>
<li>The unique identifier of the previous commit</li>
<li>Which changes that has been made to the project (files created/edited/deleted) since the previous commit</li>
<li>A human readable message explaining the changes that has been made since the previous commit</li>
<li>The name of the developer who created the commit</li>
<li>The time at which the commit was created</li>
</ul>
<p>Before you tell Git to create a new commit for you, you must first tell Git which changes you want to put into the commit (you can change files without putting those changes into the next commit you create, which might seem a bit weird, but can be useful). If you have created a new file or made changes to an existing file, you need to run the command <code>git add THE_FILE_NAME.EXT</code> to tell Git that the changes you have made to the file named <code>THE_FILE_NAME.EXT</code> should be part of the next commit you create. Run <code>git add THE_FILE_NAME.EXT</code> one time for each file you have created/changed and want to be part of the next commit.</p>
<p>Then use the command <code>git commit -m &quot;A descriptive message...&quot;</code> to tell Git to create a new commit with the added files and with the human readable message <code>A descriptive message...</code> (that you of course should change to a message describing the changes you have made since the previous commit). Git will figure out the other information (your name, previous commit unique identifier, current time, etc.) on its own or simply ask you to enter it.</p>
<p>Some useful <code>git</code> commands:</p>
<ul>
<li><code>git add THE_FILE_NAME.EXT</code> will add the file <code>THE_FILE_NAME.EXT</code> to the next commit you create</li>
<li><code>git add THE_FOLDER_NAME/*</code> will add all the files in the folder named <code>THE_FOLDER_NAME</code> to the next commit you create</li>
<li><code>git add .</code> will add all changes you have made to any file in the entire project folder</li>
<li><code>git rm THE_FILE_NAME.EXT</code> will remove the file named <code>THE_FILE_NAME.EXT</code> in the next commit you create <br>
<strong>Note</strong>: When you use <code>git rm THE_FILE_NAME.EXT</code>, the previous versions of the file <code>THE_FILE_NAME.EXT</code> in earlier commits will still be remembered by your repository, but the project folder will no longer contain the file</li>
<li><code>git status</code> will show you which files you have added/removed/&quot;changed and not added&quot; to the next commit you will create</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">Note</p>
<p>When using the various <code>git</code> commands, it will often tell you that you are working on a branch called <code>master</code> or <code>main</code>. We'll get back to what branches are in the next lecture, but for now, just accept/ignore the fact that you are working on the <code>master</code> or <code>main</code> branch.</p>
</div>
