<p>For small projects with a single developer (as in the example above), the repository usually consists of a single chain of commits, as visualized in <FigureNumber /> below (<code>Commit 4</code> is the latest commit):</p>
<Figure caption="Visualization of the commits in a repository with 4 commits.">
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>Commit 1      &lt;--      Commit 2      &lt;--      Commit 3      &lt;--      Commit 4
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div></Figure>
<p>As long as we don't make use of branches, the repository will always consist of a sequence of commits like that (we will introduce branches in next lecture).</p>
<p>To view all the commits in the repository (on the branch you are working on), you can use the command <code>git log</code> (it doesn't show how the files in each commit has been changed, but that info is stored in each commit too).</p>
<div class="custom-container tip"><p class="custom-container-title">Example (continuation of previous example)</p>
