<h3 id="switching-versions" tabindex="-1"><a class="header-anchor" href="#switching-versions" aria-hidden="true">#</a> Switching versions</h3>
<p>To view a specific version of the project, you simply tell Git <em>Show me the files as they looked like in commit <code>XXX</code></em>, where <code>XXX</code> is the unique identifier for the commit. When you do this, Git will change the files and folders in the project folder so they look precisely the way they did in commit <code>XXX</code>. The command used for this is <code>git reset XXX</code>. It is easy as that to jump between different versions of your source code! However, you better first learn about the HEAD pointer in Git before you create new commits after you have run this command.</p>
<div class="custom-container warning"><p class="custom-container-title">Note</p>
<p>You can't use the command <code>git reset XXX</code> if you have made changes to the source files you haven't committed yet (because then those changes would be lost).</p>
</div>
<h3 id="the-head-pointer" tabindex="-1"><a class="header-anchor" href="#the-head-pointer" aria-hidden="true">#</a> The HEAD pointer</h3>
<p>In Git, each repository also contains a pointer called HEAD. It points to the branch you are currently working on, but since we avoid introducing branches in this lecture, you can for now see it as that the HEAD pointer points to the last commit you've created/the commit you are currently working on. That's how Git knows which the &quot;previous commit&quot; should be when you create a new commit: it simply uses the commit the HEAD pointer points to as the previous commit, and then when you create a new commit it changes the HEAD pointer to point to the newly created commit.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>If you have created two commits, it would look like this:</p>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>                        HEAD
                          ↓
Commit 1      &lt;--      Commit 2
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>If you then create a third commit, it will look like this:</p>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>                                               HEAD
                                                 ↓
Commit 1      &lt;--      Commit 2      &lt;--      Commit 3
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>When you use the command <code>git reset XXX</code>, Git will change the files in your project folder to look the way they did in commit <code>XXX</code>, but it will not change the HEAD pointer to point to commit <code>XXX</code>. If you use the command <code>git reset XXX</code> to change the files so they look the way they did in <code>Commit 2</code> above, and then make changes and create a new commit, then you would end up with this:</p>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>                                                                      HEAD
                                                                        ↓
Commit 1      &lt;--      Commit 2      &lt;--      Commit 3      &lt;--      Commit 4
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>That is, Commit 3 will still be in the chain of commits, but Commit 4 is rather a continuation of Commit 2.</p>
</div>
<p>If you want to switch to an earlier commit and change the HEAD pointer to point to that earlier commit, you can use the command <code>git reset --hard XXX</code>.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>If you have the following:</p>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>                                               HEAD
                                                 ↓
Commit 1      &lt;--      Commit 2      &lt;--      Commit 3
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>And then use <code>git reset --hard XXX</code> to change the source files to what they looked like in Commit 2, you would end up with the following:</p>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>                        HEAD
                          ↓
Commit 1      &lt;--      Commit 2      &lt;--      Commit 3
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>If you now create a new commit, you would end up with the following:</p>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>                        HEAD
                          ↓
                       Commit 4
                          ↓
Commit 1      &lt;--      Commit 2      &lt;--      Commit 3
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div></div>
<p>Using <code>git reset --hard XXX</code> creates a new branch (or rather: the first commit after this command creates a new branch). It is not recommended to create branches this way, because you can forget commits (<code>git log</code> only displays the chain of commits the HEAD pointer points to, so you need to remember the other commits not part of this chain yourself (<code>Commit 3</code> in the example above)). So don't create branches this way; Git has better support for working with branches through various commands which we will cover in next lecture. But this serve as a good example of how you should think of commits and the HEAD pointer.</p>
<h2 id="when-to-commit" tabindex="-1"><a class="header-anchor" href="#when-to-commit" aria-hidden="true">#</a> When to commit</h2>
<p>A good question is <em>How do I know when it is time to create a new commit?</em> Is it good practice to create a new commit each day? Or should you create commits more often than that? Or less? Or is it the size of the commit (how many lines of you code you have removed/added/modified since the previous commit) that determines when to create a new commit? Are changes including 200 lines of code a good size of a commit? Well, it turns out that the best practice has nothing to do with these properties.</p>
<p>A good commit should contain a single new feature you have added to the project, or a bug that you have fixed, or similar. This way, it makes more sense to browse through the log of all commits afterwards. If it takes you 1 hour or 10 days to implement the feature/fix the bug, or if it requires changes to 20 lines of code or 2000 lines of code doesn't matter, although it is good to avoid commits with too big changes. If the feature you should implement is quite big, try to break it down into more smaller features, e.g. instead of implementing the feature <em>Manage blogposts</em> on a website, break it down into these features:</p>
<ul>
<li>Create blog posts</li>
<li>Read blog posts</li>
<li>Update blog posts</li>
<li>Delete blog posts</li>
</ul>
<p>This way, you end up with 4 smaller commits, instead of one large commit.</p>
<p>The best way to make use of a version control system is to work in a very structured way. Always before you start to write/change code in the project, you must have a goal that you want to achieve (the feature to implement or the bug to fix), and when this goal has been achieved, it is time to create a new commit.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>If the project is about writing a book, then it could be suitable to create a new commit each time you're done writing a chapter in the book. If spelling mistakes later are discovered, or if you want to improve a chapter by rephrasing sentences or paragraphs, then it could be suitable to create a new commit after each correction/rephrasing.</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>If the project is about creating a website, then it could be suitable to create a new commit each time you have implemented a new webpage on the website. If you later discover a bug or want to improve a webpage, you create a new commit when you have fixed the bug/improved that webpage.</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>If the project is about completing some lab assignments, then it could be suitable to create a new commit each time you have completed one of the assignments and each time you have fixed a mistake in an assignment you thought you had previously completed but now have corrected.</p>
</div>
<h2 id="using-git-through-a-gui" tabindex="-1"><a class="header-anchor" href="#using-git-through-a-gui" aria-hidden="true">#</a> Using Git through a GUI</h2>
<p>There exist applications with graphical user interfaces you can use to manage your Git repositories. This includes many IDE:s you can use to write source code. By using such an application, you don't need to remember all the details about the Git commands (arguments and flags), so this can be an easier way for you to get started with Git. However, these applications use the Git commands under the hood for you, so it is still good have a basic understanding of how the different Git commands work, because your workflow when using Git (creating commits, etc.) is the same no matter if you use it through a graphical user interface or a command line interface.</p>
<h2 id="practising" tabindex="-1"><a class="header-anchor" href="#practising" aria-hidden="true">#</a> Practising</h2>
<p>Play around with <a href="http://git-school.github.io/visualizing-git/#free" target="_blank" rel="noopener noreferrer">Visualizing Git<OutboundLink/></a> to learn how commits work. With this tool, you never use <code>git add</code>; just use <code>git commit -m &quot;Message&quot;</code> directly to create a new commit, and imagine that the commit contains changed/new files. You can also try to use <code>git reset --hard XXX</code>.</p>
<h2 id="recommended-reading" tabindex="-1"><a class="header-anchor" href="#recommended-reading" aria-hidden="true">#</a> Recommended reading</h2>
<p>If you want to learn more about the basics in Git, read the following chapters from <a href="https://git-scm.com/book/en/v2" target="_blank" rel="noopener noreferrer">Pro Git<OutboundLink/></a> by <em>Scott Chacon</em> and <em>Ben Straub</em> (available for free online in multiple different formats):</p>
<ul>
<li><a href="https://git-scm.com/book/en/v2/Getting-Started-About-Version-Control" target="_blank" rel="noopener noreferrer">Chapter 1: Getting Started<OutboundLink/></a></li>
<li><a href="https://git-scm.com/book/en/v2/Git-Basics-Getting-a-Git-Repository" target="_blank" rel="noopener noreferrer">Chapter 2: Git Basics<OutboundLink/></a>
<ul>
<li>You can skip the parts about using a remote repository (<code>git clone</code>, <code>git pull</code>, <code>git push</code>, ...); that's something you'll learn in the third lecture</li>
</ul>
</li>
</ul>
<p>If you find it a bit hard to start using Git, <a href="https://xkcd.com/1597/" target="_blank" rel="noopener noreferrer">you're not alone<OutboundLink/></a>.</p>
